:root {
	--text-secondary: rgba(14, 13, 15, 0.70);
	--white-contrast: #0A090A;
	--purple-main: #6F76C0;
	--mask-gradient: linear-gradient(270deg, rgba(14, 13, 5, 0.00) 0%, #ECECEC 79.5%);
	--mask-gradient-second: linear-gradient(180deg, rgba(250, 250, 250, 0.00) 0%, #FAFAFA 100%);
	--accent-gradient: linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%);
}
.hero-content {
	max-width: 1540px;
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	position: absolute;
	top: 0;
	z-index: 5;
	padding-right: var(--wp--style--root--padding-right);
	padding-left: var(--wp--style--root--padding-left);
}

.image-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	max-width: 100vw;
	height: 980px;
	object-fit: cover; 
	z-index: 0;
}

.hero-slider {
  width: 100vw;
}

.hero-button {
	border-radius: 5px;
	display: flex;
	padding: 20px 32px;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	font-weight: 700;
	line-height: 140%;
	letter-spacing: 2.1px;
	text-transform: uppercase;
	background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
	transition: all 0.3s ease-in-out;

	&.shadow {
		box-shadow: 0px 16px 24px 0px rgba(61, 175, 226, 0.46);
	} 

	&:hover {
		background: linear-gradient(90deg, #6F76C0 0%, #555ba5 100%);
		transition: all 0.3s ease-in-out;
 
		&.shadow {
			box-shadow: 0px 8px 12px -6px rgba(61, 175, 226, 0.46);
			transition: all 0.3s ease-in-out;
		}
	}
}

.wp-block-create-block-blocks-gamestore {
	border: 1px dotted #f00;
}

.image-box {
	padding: 5px;
	display: flex;
	align-items: center;
	justify-content: center;
	background: linear-gradient(90deg, rgba(216, 229, 237, 1) 0%, rgba(87, 158, 199, 0.89) 50%, rgba(210, 227, 252, 1) 100%);
	margin-bottom: 16px;
}

.hero-slider .slider-wrapper .slide-item {
  max-width: -moz-fit-content;
  max-width: fit-content;
  display: block !important;
  margin: 0px 60px;
}